<template>
    <b-container>
        
        <Table :data="rows" :header="header" :actionable="true" :createable="true" @onCreate="created" @onDelete="deleted" @onEdit="edited" :header_title="'Customer Nets'" :key="renderKey" />
    </b-container>
</template>

<script>
import Table from './Table.vue'
import axios from 'axios'

export default {
    components: {
        Table
    },
    data() {
        return {
            rows: [],
            header: [
                {
                    key: 'Id',
                    label: 'Id',
                    editable: false,
                    createable: false,
                    visible: false
                },
                {
                    key: 'BlockName',
                    label: 'Block Name',
                    editable: false,
                    createable: false,
                    visible: true,
                },
                {
                    key: 'BlockId',
                    label: 'Block Id',
                    editable: false,
                    createable: true,
                    visible: false,
                    url: 'https://portal.fl0.com/api/bitwiseag/DEV/Target/flows/blocks',
                    urls_opts: {
                        key: 'Id',
                        label: 'Name'
                    }
                },
                {
                    key: 'CustomerName',
                    label: 'Customer Name',
                    editable: false,
                    createable: false,
                    visible: true,
                },
                {
                    key: 'DayFrom',
                    label: 'Day From',
                    editable: false,
                    createable: true,
                    visible: true,
                },
                {
                    key: 'DayTo',
                    label: 'Day To',
                    editable: false,
                    createable: true,
                    visible: true,
                },
                {
                    key: 'Category',
                    label: 'Category',
                    editable: false,
                    createable: false,
                    visible: true,
                },
                {
                    key: 'NetType',
                    label: 'Net Type',
                    editable: false,
                    createable: false,
                    visible: true,
                },
                {
                    key: 'NetTypeId',
                    label: 'Net Type Id',
                    editable: false,
                    createable: true,
                    visible: false,
                    url: 'https://portal.fl0.com/api/bitwiseag/DEV/Target/flows/nettypes',
                    urls_opts: {
                        key: 'Id',
                        label: 'Name'
                    }
                }
            ],
            renderKey: 0,
        }
    },
    mounted() {
        this.populateRows()
    }, methods: {
        
        populateRows(){
            axios.defaults.headers.common['Authorization'] = "83bddc92-5840-476a-b89f-3199ff707f5f";
            return axios.get('https://portal.fl0.com/api/bitwiseag/DEV/Target/flows/customernets')
            .then(response => {
                this.rows = response.data
            })
            .catch(error => {
                console.log(error)
            })
        },
        created(item){
            axios.defaults.headers.common['Authorization'] = "83bddc92-5840-476a-b89f-3199ff707f5f";
            axios.post(`https://portal.fl0.com/api/bitwiseag/DEV/Target/flows/customernet`, {
                BlockId: item.BlockId,
                NetTypeId: item.NetTypeId,
                DayFrom: item.DayFrom,
                DayTo: item.DayTo
            }).then(() => {
                this.populateRows().then(() => {
                    this.renderKey++
                })
            })
        },
        deleted(item){
            axios.defaults.headers.common['Authorization'] = "83bddc92-5840-476a-b89f-3199ff707f5f";
            axios.delete(`https://portal.fl0.com/api/bitwiseag/DEV/Target/flows/customernet?id=${item.Id}`)
            .then(() => {
                this.populateRows().then(() => {
                    this.renderKey++
                })
            })
        },
        edited(item){
            axios.defaults.headers.common['Authorization'] = "83bddc92-5840-476a-b89f-3199ff707f5f";
            axios.put(`https://portal.fl0.com/api/bitwiseag/DEV/Target/flows/customernet?id=`+item.Id, {
                BlockId: item.BlockId,
                NetTypeId: item.NetTypeId,
                DayFrom: item.DayFrom,
                DayTo: item.DayTo
            }).then(() => {
                this.populateRows().then(() => {
                    this.renderKey++
                })
            })
        }
    }
}
</script>