<template>
    <b-container>
        <Table :data="rows" :header="header" :actionable="true" :createable="true" :header_title="'Blocks'" @onEdit="edited" @onDelete="deleted" @onCreate="created" :key="renderKey" />
    </b-container>
</template>

<script>
import Table from './Table.vue'
import axios from 'axios'

export default {
    components: {
        Table
    },
    data() {
        return {
            rows: [],
            header: [{
                    key: 'Id',
                    label: 'Id',
                    editable: false,
                    createable: false,
                    visible: false
                },
                {
                    key: 'Name',
                    label: 'Name',
                    editable: true,
                    createable: true,
                    visible: true
                },
                {
                    key: 'customerName',
                    label: 'Customer Name',
                    editable: false,
                    createable: false,
                    visible: true
                },{
                    key: 'customerId',
                    label: 'Customer Id',
                    editable: true,
                    createable: true,
                    visible: false,
                    url: 'https://portal.fl0.com/api/bitwiseag/DEV/Target/flows/customers',
                    urls_opts: {
                        key: 'customerId',
                        label: 'customerName'
                    }
                }
                ],
            renderKey: 0
        }
    },
    mounted() {
        this.populateData()
    },
    methods: {
        populateData(){
            this.rows= []
            axios.defaults.headers.common['Authorization'] = "83bddc92-5840-476a-b89f-3199ff707f5f";
            return axios.get('https://portal.fl0.com/api/bitwiseag/DEV/Target/flows/blocks')
            .then(response => {
                let rows_data = response.data
                for(var i = 0; i < rows_data.length; i++) {
                    this.rows.push({
                        Id: rows_data[i].Id,
                        Name: rows_data[i].Name,
                        customerName: rows_data[i].CustomerId.Name,
                        customerId: rows_data[i].CustomerId.Id
                    })
                }

            })
            .catch(error => {
                console.log(error)
            })
        },
        edited(row) {
            axios.defaults.headers.common['Authorization'] = "83bddc92-5840-476a-b89f-3199ff707f5f";
            axios.put('https://portal.fl0.com/api/bitwiseag/DEV/Target/flows/block?id=' + row.Id, {
                Name: row.Name,
                CustomerId: row.customerId
            }).then(() => {
                this.populateData().then(() => {
                    this.renderKey++
                })
            })
        },
        deleted(row){
            axios.defaults.headers.common['Authorization'] = "83bddc92-5840-476a-b89f-3199ff707f5f";
            axios.delete('https://portal.fl0.com/api/bitwiseag/DEV/Target/flows/block?id=' + row.Id).then(() => {
                this.populateData().then(() => {
                    this.renderKey++
                })
            })            
        },
        created(row){
            axios.defaults.headers.common['Authorization'] = "83bddc92-5840-476a-b89f-3199ff707f5f";
            axios.post('https://portal.fl0.com/api/bitwiseag/DEV/Target/flows/block', {
                Name: row.Name,
                CustomerId: row.customerId
            }).then(() => {
                this.populateData().then(() => {
                    this.renderKey++
                })
            })
        }
    }
}
</script>