<template>

    <b-container fluid>
        
        <div class="row">
            <div class="col-md-6">
                <Table :data="rows" :header="header" @onEdit="edited" @onDelete="deleted" @onCreate="created" @onSelect="selected" :actionable="true" :key="renderKey" header_title="Customers"/>
                <b-pagination
                    v-model="currentPage"
                    :total-rows="totalRows"
                    :per-page="perPage"
                    @change="pageChanged"
                >
                </b-pagination>
            </div>
            <div class="col-md-6">
                <Table :data="netsUsedRows" :header="netsUsedHeader" :key="netsUsedRenderKey" :actionable="false" :createable="false" header_title="Customer Details" />
            </div>
        </div>
    </b-container>
    
</template>

<script>
import Table from './Table.vue'
import axios from 'axios'

export default {
    components: {
        Table
    },
    data(){
        return {
            rows: [],
            header: [{
                    key: 'id',
                    label: 'Customer Id',
                    editable: false,
                    createable: true,
                    visible: true,
                },
                {
                    key: 'name',
                    label: 'Name',
                    editable: true,
                    createable: true,
                    visible: true,                    
                },
                {
                    key: 'blockCount',
                    label: 'Block Count',
                    editable: false,
                    createable: false,
                    visible: true,
                }],
            renderKey: 0,

            currentPage: 1,
            perPage: 10,
            totalRows: 110,

            netsUsedRenderKey: -1,
            netsUsedRows: [],
            netsUsedHeader: [
                {
                    key: 'BlockName',
                    label: 'Block Name',
                    editable: false,
                    createable: false,
                    visible: true,
                },
                {
                    key: 'Category',
                    label: 'Category',
                    editable: false,
                    createable: false,
                    visible: true,
                },
                {
                    key: 'DayFrom',
                    label: 'Day From',
                    editable: false,
                    createable: false,
                    visible: true,
                },
                {
                    key: 'DayTo',
                    label: 'Day To',
                    editable: false,
                    createable: false,
                    visible: true,
                }
            ]
        }
    },
    mounted() {
        axios.defaults.headers.common['Authorization'] = "83bddc92-5840-476a-b89f-3199ff707f5f";
        axios.get('https://portal.fl0.com/api/bitwiseag/DEV/Target/flows/customers')
        .then(response => {
            this.rows = response.data
        })
        .catch(error => {
            console.log(error)
        })
    },
    methods: {
        populateData(){
            axios.defaults.headers.common['Authorization'] = "83bddc92-5840-476a-b89f-3199ff707f5f";
            return axios.get('https://portal.fl0.com/api/bitwiseag/DEV/Target/flows/customers?page=' + this.currentPage + '&page_size=' + this.perPage)
            .then(response => {
                this.rows = response.data
            })
            .catch(error => {
                console.log(error)
            })
        },
        pageChanged(page){
            this.currentPage = page
            this.populateData()
        },
        edited(item){
            axios.defaults.headers.common['Authorization'] = "83bddc92-5840-476a-b89f-3199ff707f5f";
            axios.put(`https://portal.fl0.com/api/bitwiseag/DEV/Target/flows/customer?id=${item.id}`, {
                CustomerId: item.id,
                Name: item.name
            })
            .then(() => {
                this.populateData().then(() => {
                    this.renderKey++
                })
            })
        },
        deleted(item){
            axios.defaults.headers.common['Authorization'] = "83bddc92-5840-476a-b89f-3199ff707f5f";
            axios.delete(`https://portal.fl0.com/api/bitwiseag/DEV/Target/flows/customer?id=${item.id}`)
            .then(() => {
                this.populateData().then(() => {
                    this.renderKey++
                })
            }) 
        },
        created(item){
            axios.defaults.headers.common['Authorization'] = "83bddc92-5840-476a-b89f-3199ff707f5f";
            axios.post(`https://portal.fl0.com/api/bitwiseag/DEV/Target/flows/customer`, {
                CustomerId: item.id,
                Name: item.name
            })
            .then(() => {
                this.populateData().then(() => {
                    this.renderKey++
                })
            }) 
        },
        selected(item){
            if(item){
                this.netsUsedRows = item.netsUsed
                console.log(item.netsUsed);
                this.netsUsedRenderKey--
            }
        }
    }
}
</script>