<template>
    <b-container>
        <Table :data="rows" :header="header" :actionable="true" :createable="true" @onEdit="edited" @onDelete="deleted" @onCreate="created" :header_title="'Net Types'" :key="renderKey" />
    </b-container>
</template>

<script>
import Table from './Table.vue'
import axios from 'axios'

export default {
    components: {
        Table
    },
    data() {
        return {
            rows: [],
            header: [{
                    key: 'Id',
                    label: 'Id',
                    editable: false,
                    createable: false,
                    visible: false,
                },
                {
                    key: 'Name',
                    label: 'Name',
                    editable: true,
                    createable: true,
                    visible: true
                },
                {
                    key: 'Category',
                    label: 'Category',
                    editable: false,
                    createable: true,
                    visible: true,
                    choices: [
                        "Vineyard",
                        "Berries"
                    ]
                }],
            renderKey: 0,
        }
    },
    mounted() {
        this.populateData()
    },
    methods: {
        
        populateData(){
            axios.defaults.headers.common['Authorization'] = "83bddc92-5840-476a-b89f-3199ff707f5f";
            return axios.get('https://portal.fl0.com/api/bitwiseag/DEV/Target/flows/nettypes')
            .then(response => {
                this.rows = response.data
            })
            .catch(error => {
                console.log(error)
            })
        },
        edited(row) {
            axios.defaults.headers.common['Authorization'] = "83bddc92-5840-476a-b89f-3199ff707f5f";
            axios.put('https://portal.fl0.com/api/bitwiseag/DEV/Target/flows/nettype?id=' + row.Id, {
                Name: row.Name,
                Category: row.Category
            }).then(() => {
                this.populateData().then(() => {
                    this.renderKey++
                })
            })
        },
        deleted(row) {
            axios.defaults.headers.common['Authorization'] = "83bddc92-5840-476a-b89f-3199ff707f5f";
            axios.delete('https://portal.fl0.com/api/bitwiseag/DEV/Target/flows/nettype?id=' + row.Id)
            .then(() => {
                this.populateData().then(() => {
                    this.renderKey++
                })
            })
        },
        created(row) {
            axios.defaults.headers.common['Authorization'] = "83bddc92-5840-476a-b89f-3199ff707f5f";
            axios.post('https://portal.fl0.com/api/bitwiseag/DEV/Target/flows/nettype', {
                Name: row.Name,
                Category: row.Category
            }).then(() => {
                this.populateData().then(() => {
                    this.renderKey++
                })
            })
        }
    }
}
</script>